import { Center, Flex, Image, Text } from "@chakra-ui/react";

export function FirstOverviewCategoryMobile() {
    return (
        <>
            <Flex
                w="100%"
                margin="15px 0 15px 0"
                px="20px"
                justifyContent="space-between"
            >
                <Center flexDir="column">
                    <Center
                        width="170px"
                        height="170px"
                        border="1px solid #c1c1c1"
                        color="rgba(0,0,0,0.5)"
                        fontSize="56px"
                        lineHeight="0.9"
                        fontWeight="700"
                        flexDir="column"
                    >
                        <p>20</p>
                        <Text fontSize="32px" fontWeight="400">
                            YEARS
                        </Text>
                    </Center>
                    <Text fontSize="14px" mt="15px" fontWeight="600">
                          20 
                    </Text>
                </Center>

                <Center flexDir="column">
                    <Center
                        width="170px"
                        height="170px"
                        border="1px solid #c1c1c1"
                        color="rgba(0,0,0,0.5)"
                        fontSize="54px"
                        lineHeight="0.9"
                        fontWeight="700"
                        flexDir="column"
                    >
                        <Text fontWeight="400" fontSize="36px">
                            SINCE
                        </Text>
                        <Text fontWeight="700">2003</Text>
                    </Center>
                    <Text fontSize="14px" mt="15px" fontWeight="600">
                        2003  
                    </Text>
                </Center>
            </Flex>

            <Flex w="100%" px="20px" justifyContent="space-between">
                <Center flexDir="column">
                    <Center
                        width="170px"
                        height="170px"
                        border="1px solid #c1c1c1"
                        color="rgba(0,0,0,0.5)"
                        flexDir="column"
                    >
                        <Image
                            w="100%"
                            h="100%"
                            filter="grayscale(100%)"
                            src={require(`../../assets/hat.jpg`)}
                            alt=""
                        />
                    </Center>
                    <Text fontSize="14px" mt="15px" fontWeight="600">
                        
                    </Text>
                </Center>
                <Center flexDir="column">
                    <Center
                        width="170px"
                        height="170px"
                        border="1px solid #c1c1c1"
                        color="rgba(0,0,0,0.5)"
                        flexDir="column"
                    >
                        <Image
                            w="100px"
                            h="130px"
                            filter="grayscale(100%)"
                            src={require(`../../assets/_.png`)}
                            alt=" "
                        />
                    </Center>
                    <Text fontSize="14px" mt="15px" fontWeight="600">
                         
                    </Text>
                </Center>
            </Flex>
        </>
    );
}
