import { Box, Center, Heading, Text } from "@chakra-ui/react";
import styled from "styled-components";

const InnerText = styled.span`
    font-weight: 600;
    color: #603988;
`;

export function PromiseMobile() {
    return (
        <>
            <Box
                width="100%"
                h="100%"
                position="relative"
                backgroundColor="white"
                py="50px"
            >
                <Center h="100%" m="0 auto" flexDir="column">
                    <Box w="50px" borderBottom="2px solid #603988"></Box>
                    <Heading
                        fontSize="24px"
                        color="#222"
                        my="20px"
                        fontWeight="600"
                    >
                         
                    </Heading>
                    <Text
                        textAlign="center"
                        color="#bfbfbf"
                        fontWeight="300"
                        letterSpacing="4px"
                        fontSize="10px"
                    >
                        OUR PROMISES
                    </Text>
                    <Box
                        position="relative"
                        w="100%"
                        h="180px"
                        my="30px"
                        backgroundSize="cover"
                        backgroundImage={require("../../../assets/dentist6.jpg")}
                    ></Box>
                    <Text
                        fontSize="18px"
                        backgroundColor="white"
                        zIndex="1"
                        px="15px"
                        mb="20px"
                    >
                        <b> </b> <b> </b> 
                    </Text>
                    <Text fontSize="13px" px="15px">
                             {" "}
                        <InnerText>  </InnerText>
                        .
                        <br></br>
                              
                        .<br></br>
                         {" "}
                        <InnerText>  ,  </InnerText>
                        .
                    </Text>
                </Center>
            </Box>
        </>
    );
}
