import { Box, Center, Heading, Text } from "@chakra-ui/react";

export function Invisalign() {
    return (
        <>
            <Box
                width="100%"
                h="1000px"
                position="relative"
                backgroundPosition="top center"
                backgroundColor="white"
                backgroundRepeat="no-repeat"
                backgroundImage={require("../../../assets/premium-1.jpg")}
            >
                <Center
                    maxWidth="1200px"
                    m="auto"
                    flexDir="column"
                    py="100"
                    color="white"
                >
                    <Box w="100px" borderBottom="2px solid black"></Box>
                    <Text mt="40px" fontSize="30px">
                          
                    </Text>
                    <Heading fontSize="60px" fontWeight="600">
                        
                    </Heading>
                    <Text
                        color="#222"
                        fontWeight="300"
                        letterSpacing="14px"
                        ml="8px"
                    >
                        INVISALIGN
                    </Text>
                </Center>
                <Box maxWidth="1200px" m="auto">
                    <Box
                        w="250px"
                        h="50px"
                        backgroundRepeat="no-repeat"
                        backgroundImage={require("../../../assets/invisalign.png")}
                        my="40px"
                    ></Box>
                    <Text
                        fontSize="48px"
                        px="15px"
                        color="black"
                        fontWeight="600"
                    >
                        "   "
                    </Text>
                    <Text pl="10px" fontSize="24px" color="rgba(0,0,0,0.6)">
                            
                        .<br></br>
                           
                        !
                    </Text>
                </Box>
            </Box>
        </>
    );
}
