import { Center, Text } from "@chakra-ui/react";
import styled from "styled-components";

const Container = styled.div`
    width: 180px;
    height: 240px;
    position: relative;
    color: #666;
    cursor: pointer;
    overflow: hidden;
`;
const InnerImg = styled.img`
    width: 100%;
    height: 100%;
    alt: "overview";
    transition: scale 0.5s;
    ${Container}:hover & {
        scale: 1.1;
    }
`;
const InnerBox = styled.div`
    position: absolute;
    top: 30%;
    left: 20px;
    color: rgba(0, 0, 0, 0.9);
    font-size: 24px;
    text-align: start;
`;
const InnerText = styled.p`
    position: absolute;
    top: 4px;
    left: 6px;
    font-size: 26px;
    font-weight: 100;
`;

export function SecondOverviewCategoryMobile() {
    return (
        <>
            <Center gap="10px" m="10px 0">
                <Container>
                    <InnerImg
                        src={require(`../../assets/overview-1.jpg`)}
                        alt="Fisrt pic."
                    />
                    <InnerBox>
                        <Text>P</Text>
                        <InnerText>+</InnerText>
                        <Text fontSize="14px" fontWeight="600">
                            
                        </Text>
                        <Text fontSize="10px">
                             <br></br> 
                            <br></br>.
                        </Text>
                    </InnerBox>
                </Container>
                <Container>
                    <InnerImg
                        src={require(`../../assets/overview-2.jpg`)}
                        alt="Second pic."
                    />
                    <InnerBox style={{ top: "50%", color: "white" }}>
                        <Text>P</Text>
                        <InnerText>+</InnerText>
                        <Text fontSize="14px" fontWeight="600">
                            
                        </Text>
                        <Text fontSize="10px">
                             ,<br></br>  .
                        </Text>
                    </InnerBox>
                </Container>
            </Center>
            <Center gap="10px">
                <Container>
                    <InnerImg
                        src={require(`../../assets/overview-3.jpg`)}
                        alt="Third pic."
                    />
                    <InnerBox>
                        <Text>P</Text>
                        <InnerText>+</InnerText>
                        <Text fontSize="14px" fontWeight="600">
                            Natural  
                        </Text>
                        <Text fontSize="10px">
                             ,<br></br> .
                        </Text>
                    </InnerBox>
                </Container>
                <Container>
                    <InnerImg
                        src={require(`../../assets/overview-4.jpg`)}
                        alt="Last pic."
                    />
                    <InnerBox style={{ top: "50%" }}>
                        <Text>P</Text>
                        <InnerText>+</InnerText>
                        <Text fontSize="14px" fontWeight="600">
                            
                        </Text>
                        <Text fontSize="10px">
                              ,<br></br> 
                            .
                        </Text>
                    </InnerBox>
                </Container>
            </Center>
        </>
    );
}
