import { Box, Text } from "@chakra-ui/react";
import styled from "styled-components";

const UpperText = styled.span`
    padding: 0 15px;
    border-right: 1px solid #d0d0d0;
    line-height: 1;
    &:hover {
        color: white;
    }
    &:last-child {
        border-right: none;
    }
`;

export function Footer() {
    return (
        <>
            <Box w="100%" backgroundColor="#373737">
                <Box backgroundColor="#4e4e4e">
                    <Box
                        maxWidth="1200px"
                        margin="auto"
                        py="20px"
                        color="#D0D0D0"
                        fontWeight="500"
                        fontSize="14px"
                        cursor="pointer"
                    >
                        <UpperText> </UpperText>
                        <UpperText> </UpperText>
                        <UpperText></UpperText>
                    </Box>
                </Box>
                <Box
                    maxWidth="1200px"
                    margin="auto"
                    py="20px"
                    color="#D0D0D0"
                    fontSize="14px"
                >
                    <Text px="15px">   1</Text>
                    <Text px="15px">
                         :   :
                         TEL : 031.111.2222  : 123-45-678901
                    </Text>

                    <Text px="15px">
                        COPYRIGHT  2023  ALL RIGHT
                        RESERVED.
                    </Text>
                </Box>
            </Box>
        </>
    );
}
