import { Box, Center, Heading, Slide, Text } from "@chakra-ui/react";
import { FirstOverviewCategoryMobile } from "../../../components/main_overview_category_mobile/first_overview_category_mobile";

export function FirstOverViewMobile() {
    return (
        <>
            <Slide
                in={true}
                direction="bottom"
                transition={{ enter: { duration: 0.5 } }}
                style={{
                    position: "relative",
                    width: "100%",
                    marginTop: "50px",
                    paddingBottom: "20px",
                }}
            >
                <Box w="100%" h="100%" textAlign="center">
                    {/*     */}
                    <Box margin="0 auto" position="relative" textAlign="center">
                        <Box
                            w="90%"
                            position="absolute"
                            left="22px"
                            top="15px"
                            borderBottom="1px"
                            borderStyle="solid"
                            borderColor="#c1c1c1"
                        ></Box>
                        <Center
                            width="50%"
                            position="relative"
                            margin="0 auto"
                            backgroundColor="white"
                            fontSize="16px"
                            fontWeight="100"
                            color="#603988"
                        >
                            <Text
                                lineHeight="1.2"
                                px="5px"
                                mr="5px"
                                fontSize="28px"
                                color="white"
                                backgroundColor="#603988"
                                fontWeight="600"
                            >
                                1
                                <em
                                    style={{
                                        fontSize: "16px",
                                        fontWeight: "400",
                                    }}
                                >
                                    %
                                </em>
                            </Text>
                            <Text mr="5px"></Text>
                            <Text mr="5px" fontWeight="600">
                                
                            </Text>
                            
                        </Center>
                        <Text
                            fontWeight="200"
                            letterSpacing="4px"
                            margin="15px 0 30px 0px"
                            fontSize="10px"
                        >
                            KOREA ORTHODONTIC CLINIC
                        </Text>
                    </Box>
                    <Heading
                        color="#444"
                        fontWeight="600"
                        fontSize="14px"
                        mb="10px"
                    >
                          2003...
                    </Heading>
                    <Text fontSize="12px" color="#444" display="block">
                             
                        <br></br>
                             
                        .
                    </Text>
                </Box>
                <FirstOverviewCategoryMobile></FirstOverviewCategoryMobile>
            </Slide>
        </>
    );
}
