import { Box, Center, Heading, Text } from "@chakra-ui/react";
import styled from "styled-components";

const InnerText = styled.span`
    font-weight: 600;
    color: #603988;
`;

export function Promise() {
    return (
        <>
            <Box
                width="100%"
                h="1400px"
                position="relative"
                backgroundColor="white"
            >
                <Center maxWidth="1200px" h="100%" m="0 auto" flexDir="column">
                    <Box w="100px" borderBottom="2px solid #603988"></Box>
                    <Heading
                        fontSize="60px"
                        color="#222"
                        my="10px"
                        fontWeight="600"
                    >
                         
                    </Heading>
                    <Text
                        textAlign="center"
                        color="#bfbfbf"
                        fontWeight="300"
                        letterSpacing="10px"
                    >
                        OUR PROMISES
                    </Text>
                    <Box
                        position="relative"
                        w="100%"
                        h="600px"
                        my="70px"
                        backgroundSize="cover"
                        backgroundImage={require("../../../assets/dentist6.jpg")}
                    >
                        <Box
                            w="100%"
                            position="absolute"
                            bottom="-100px"
                            borderBottom="1px"
                            borderStyle="solid"
                            borderColor="#c1c1c1"
                        ></Box>
                    </Box>
                    <Text
                        fontSize="40px"
                        backgroundColor="white"
                        zIndex="1"
                        px="15px"
                        mb="20px"
                    >
                        <b> </b> <b> </b> 
                    </Text>
                    <Text fontSize="21px">
                             {" "}
                        <InnerText>  </InnerText>.
                        <br></br>
                              
                        .<br></br>
                         {" "}
                        <InnerText>  ,  </InnerText>
                        .
                    </Text>
                </Center>
            </Box>
        </>
    );
}
