import { Center, Text } from "@chakra-ui/react";
import styled from "styled-components";

const Container = styled.div`
    width: 375px;
    height: 490px;
    position: relative;
    color: #666;
    cursor: pointer;
    overflow: hidden;
`;
const InnerImg = styled.img`
    width: 100%;
    height: 100%;
    alt: "overview";
    transition: scale 0.5s;
    ${Container}:hover & {
        scale: 1.1;
    }
`;
const InnerBox = styled.div`
    position: absolute;
    top: 35%;
    left: 20px;
    color: rgba(0, 0, 0, 0.9);
    font-size: 44px;
    text-align: start;
`;
const InnerText = styled.p`
    position: absolute;
    top: 22px;
    left: 13px;
    font-size: 32px;
    font-weight: 100;
`;

export function SecondOverviewCategory() {
    return (
        <>
            <Center mt="20px" gap="40px">
                <Container>
                    <InnerImg
                        src={require(`../../assets/overview-1.jpg`)}
                        alt="First picture"
                    />
                    <InnerBox>
                        <Text>P</Text>
                        <InnerText>+</InnerText>
                        <Text fontSize="24px" fontWeight="600">
                            
                        </Text>
                        <Text fontSize="16px" pl="2px" mt="5px">
                             <br></br> 
                            <br></br>.
                        </Text>
                    </InnerBox>
                </Container>
                <Container>
                    <InnerImg
                        src={require(`../../assets/overview-2.jpg`)}
                        alt="Second picture"
                    />
                    <InnerBox style={{ top: "58%", color: "white" }}>
                        <Text>P</Text>
                        <InnerText>+</InnerText>
                        <Text fontSize="24px" fontWeight="600">
                            
                        </Text>
                        <Text fontSize="16px" pl="2px" mt="5px">
                             ,<br></br>  .
                        </Text>
                    </InnerBox>
                </Container>
                <Container>
                    <InnerImg
                        src={require(`../../assets/overview-3.jpg`)}
                        alt="Third picture"
                    />
                    <InnerBox>
                        <Text>P</Text>
                        <InnerText>+</InnerText>
                        <Text fontSize="24px" fontWeight="600">
                            Natural  
                        </Text>
                        <Text fontSize="16px" pl="2px" mt="5px">
                             ,<br></br> .
                        </Text>
                    </InnerBox>
                </Container>
                <Container>
                    <InnerImg
                        src={require(`../../assets/overview-4.jpg`)}
                        alt="Fourth picture"
                    />
                    <InnerBox style={{ top: "58%" }}>
                        <Text>P</Text>
                        <InnerText>+</InnerText>
                        <Text fontSize="24px" fontWeight="600">
                            
                        </Text>
                        <Text fontSize="16px" pl="2px" mt="5px">
                              ,<br></br> 
                            .
                        </Text>
                    </InnerBox>
                </Container>
            </Center>
        </>
    );
}
